.calc {
    background-color: #f7f7f7;
    padding: 20px;
    margin: 20px;
    border: 1px solid #d0d0d0;
    border-top: 1px solid #28a745;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
}

.calc h3 {
    margin-top: 0;
    font-size: 1.5rem;
    font-weight: bold;
    text-align: center;
}

.flex-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
    margin-top: 30px;
}

.flex-item {
    padding: 20px;
    border: 1px solid #d0d0d0;
    border-top: 1px solid #28a745;
    background-color: #ffffff;
    margin: 20px;
    flex: 1 1 40%;
    max-width: 400px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
}

.flex-item:first-child {
    margin-left: 0;
}

.flex-item:last-child {
    margin-right: 0;
}

.flex-item div {
    margin-bottom: 20px;
}

.calc ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.calc ul li {
    font-size: 2rem;
    text-align: right;
    font-weight: bold;
}

.calc input[type="number"] {
    margin-right: 10px;
    margin-bottom: 10px;
    padding: 10px;
    border: 1px solid #d0d0d0;
    border-radius: 5px;
    font-size: 1.2rem;
}

.calc select {
    margin-right: 10px;
    margin-bottom: 10px;
    padding: 10px;
    border: 1px solid #d0d0d0;
    border-radius: 5px;
    font-size: 1.2rem;
}

.calc input[type="submit"] {
    margin-left: 10px;
    padding: 10px 20px;
    background-color: #28a745;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1.2rem;
}

.block>div:nth-child(2) {
    margin-top: 30px;
}

.block input[type="submit"] {
    margin-left: 10px;
    padding: 10px 20px;
    background-color: #28a745;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1.2rem;
}