.rate {
    background-color: #eee;
    padding: 20px;
    margin: 20px;
    border: 1px solid #eee;
    border-radius: 5px;
}

.rate h3 {
    margin-top: 0;
    margin-bottom: 20px;
    letter-spacing: 1px;
    font-size: 2rem;
}

.flex-container {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: flex-start;
}

.flex-item {
    padding: 20px;
    border: 1px solid #eee;
}